import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

import VueLazyload from 'vue-lazyload'
import defaultImgCover from '@/assets/images/lazy-load.png'

Vue.use(VueLazyload, {
    preLoad: 1.3,
    loading: defaultImgCover,
    attempt: 1
})

Vue.config.productionTip = false


new Vue({
    router,
    store,
    render: h => h(App)
}).$mount('#app')
