import axios from 'axios'
import jsonp from 'jsonp'

const HTTP = {
    get: (url, params) => {
        return new Promise((resolve, reject) => {
            // axios(url)
            // .then(res => {
            //     resolve(res)
            // })
            // .catch(err => {
            //    reject(err) 
            // })

            jsonp(url, params, (err, data) => {
                if(!err) {
                    resolve(data)
                } else {
                    reject(err)
                }
            })
        })
    }
}
export default HTTP