import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

const router = new Router({
    mode: 'hash',
    routes: [{
            path: '/',
            name: 'home',
            component: () => import('./views/Home')
        },
        {
            path: '/singer',
            name: 'singer',
            component: () => import('./views/Singer')
        },
        {
            path: '/singerDetail/:singer_mid',
            name: 'singerDetail',
            component: () => import('./views/SingerDetail')
        },
        {
            path: '/search',
            name: 'search',
            component: () => import('./views/Search')
        },
        {
            path: '/play/:song_mid',
            name: 'play',
            component: () => import('./views/Playing')
        },
        {
          path: '*',
          redirect: { name: 'home' }
        }]
})

export default router;

