<template>
    <div class="mini-player" v-if="curSong" @click="goPlayPage">
        <div class="mini-player-con">
            <!-- S 当前歌曲封面 -->
            <div class="thumb" :class="[playState == 'playing' ? 'playing-run' : '']">
                <img :src="'https://y.gtimg.cn/music/photo_new/T002R90x90M000'+ curSong.albummid +'.jpg?max_age=2592000'" alt="">
            </div>
            <!-- E 当前歌曲封面 -->
            
            <!-- S 歌曲信息 -->
            <div class="song-info">
                <h3>歌曲名称歌曲名称歌曲名称歌曲名称歌曲名称</h3>
                <span v-text="curSong.singer[0].name"></span>
            </div>
            <!-- E 歌曲信息 -->

            <!-- S 操作 -->
            <div class="opts" @click.stop>
                <a class="play-pused" :class="[playState == 'playing' ? 'pused' : 'play']" href="javascript:"  @click="playState == 'playing' ? paused() : play()"></a>
                <a class="list" href="javascript:" @click="openList"></a>
            </div>
            <!-- E 操作 -->
        </div>

        <div class="song-list" :class="[listState ? 'open' : 'close']" @click.stop="closeList">
            <div class="list-operator" @click.stop>
                <a class="play-mode" href="javascript:">
                    <i></i>
                    顺序播放
                </a>
                <a class="del" href="javascript:"></a>
            </div>
            <ul class="songs" @click.stop>
                <li v-for="(song, i) in songList" :class="[i === curIndex ? 'current' : '']" @click="selectPlay(i)">
                    <h3 v-text="song.songname"></h3>
                    <div class="opts">
                        <span class="like"></span>
                        <span class="del"></span>
                    </div>
                </li>
            </ul>
            <div class="close">关闭列表</div>
        </div>

        <!-- S 播放器 -->
        <div class="player">
            <audio autoplay :src="curSong.src" controls ref="player" @ended="playEnded"></audio>
            <p>{{curSong.src}}</p>
        </div>
        <!-- E 播放器 -->
    </div>
</template>

<script>
import HTTP from '@/utils/http'
import {mapGetters, mapMutations} from 'vuex'
export default {
    data: function() {
        return {
            playState: 'paused',
            songMid: '',
            playSrc: '',
            listState: false
        }
    },
    computed: {
        ...mapGetters({
            curSong: 'getCurSong',
            songList: 'getPlayList',
            curIndex: 'getCurIndex'
        })
    },
    watch: {
        curSong: function(newSong, oldSong) {
            // 1、获取歌曲 mid
            this.songMid = newSong.songmid || ''
            // 2、获取vKey
            this.getVKey()
            .then(vkey => {
                // 3、组装播放地址
                this.getPlaySrc(vkey)
            })
            .catch(error => {
            })
        }
    },
    created() {
        this.playState = 'playing'
    },
    methods: {
        ...mapMutations([
            'ADD_SRC_TO_SONG',
            'SET_CUR_INDEX'
        ]),
        // 获取 vKey
        getVKey() {
            return new Promise((resolve, reject) => {
                let vKeyApi = `https://c.y.qq.com/base/fcgi-bin/fcg_music_express_mobile3.fcg?g_tk=1928093487&inCharset=utf-8&outCharset=utf-8&notice=0&format=json&cid=205361747&platform=yqq&hostUin=0&needNewCode=0&uin=0&songmid=${this.songMid}&filename=C400${this.songMid}.m4a&guid=7120953682`
                HTTP.get(vKeyApi, {})
                .then(({data: {items}}) => {
                    if (items[0] && items[0].vkey) {
                        resolve(items[0].vkey)
                    } else {
                        reject(new Error('vKey获取失败'))
                    }
                })
            })
        },
        // 组装播放地址, 并将播放地址合并保存到播放歌曲信息
        getPlaySrc(vKey) {
            this.playSrc = `http://dl.stream.qqmusic.qq.com/C400${this.songMid}.m4a?vkey=${vKey}&guid=7120953682&uin=0&fromtag=66`
            this.ADD_SRC_TO_SONG(this.playSrc)
            this.$forceUpdate()
        },
        // 当前歌曲播放结束
        playEnded() {
            let len = this.songList.length
            let index = this.curIndex + 1 > len - 1 ? 0 : this.curIndex + 1
            this.SET_CUR_INDEX(index)

            this.$refs.player.play()
        },
        // 暂停
        paused() {
            this.playState = 'pused'
            this.$refs.player.pause() 
        },
        // 播放
        play() {
            this.playState = 'playing'
            this.$refs.player.play()
        },
        // 跳转到播放页面
        goPlayPage() {
            this.$router.push({ name: 'play', params: { 'song_mid':  this.songMid}})
        },
        // 歌曲播放列表，选择播放
        selectPlay(index) {
            this.SET_CUR_INDEX(index)
        },
        // 列表的关闭
        closeList() {
            this.listState = false
        },
        // 列表的打开
        openList() {
            this.listState = true
        }
    },
    components: {
    }
}
</script>
<style scoped lang="scss">
@import 'SCSS/miniPlayer';
</style>