const getters = {
    // 获取播放列表
    getPlayList: state => {
        return state.player.songs
    },
    // 获取当前播放歌曲
    getCurSong: state => {
        let player = state.player
        return player.songs[player.curIndex]
    },
    // 获取当前歌曲索引
    getCurIndex: state => state.player.curIndex

}

export default getters