import * as types from './mutation_types';

const mutations = {
    // 添加歌曲到播放列表
    [types.ADD_TO_SONG_LIST]: (state, song) => {
        let player = state.player
        let hasCount = 0;
        player.songs.forEach((s, i) => {
            if (song.songmid === s.songmid) {
                hasCount++

                // 将此首存在的歌曲作为当前播放
                player.curIndex = i
            }
        })
        
        if (hasCount > 0) {
            return '此歌曲已存在播放列表中，不能重复添加'
        }
        player.songs.push(song)
        // 修改当前播放歌曲索引
        player.curIndex = player.songs.length - 1
    },
    // 添加 播放地址到歌曲信息
    [types.ADD_SRC_TO_SONG]: (state, src) => {
        let player = state.player
        player.songs[player.curIndex].src = src
    },
    // 设置当前播放歌曲索引
    [types.SET_CUR_INDEX]: (state, index) => {
        state.player.curIndex = index
    } 
}
export default mutations;